// Initialize Firebase
const FirebaseApp = initializeApp(firebaseConfig);
const db = getFirestore(FirebaseApp);

export async function getAllCharacters() {
  const docsSnap = await getDocs(collection(db, "characters"));

  const charactersData = docsSnap.docs.map((element) => {
    return { id: element.id, ...element.data() };
  });

  return charactersData;
}

export async function getCharacter(id) {
  const characterRef = doc(db, "characters", id);
  const docSnap = await getDoc(characterRef);

  const characterData = { id: docSnap.id, ...docSnap.data() };

  return characterData;
}

export async function getCharactersByCategory(category) {
  const collectionRef = collection(db, "characters");
  const categoryRef = query(collectionRef, where("category", "==", category));
  const docsSnap = await getDocs(categoryRef);

  const categoryData = docsSnap.docs.map((element) => {
    return { id: element.id, ...element.data() };
  });

  return categoryData;
}

export async function createBuyerOrder(orderData) {
  const collectionRef = collection(db, "orders");

  let resp = await addDoc(collectionRef, orderData);

  return resp.id;
}

